@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.AccountsInfoWrapper {
    display: flex;
    justify-content: center;
    background-color: rgb(168, 236, 113);
    background: #FFFFFF;
    border-radius: 40px 0px 0px 0px;
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 27px;
    letter-spacing: 0.374px;
    color: #B5B5B5;
    padding-bottom: 50px;
}

.blackText {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.374px;
    color: #262626;
}

.infoTitle{
    margin-top: 33px;
}

.infoContainer {
    width: 90%;
}

.transactionTo {
    width: 80%;
}

.topUpBtn {
    border-color: #FD3A6C !important;
    border-radius: 20px !important;
    padding: 20px !important;
}

.topUpBtn:hover {
    background-color: #fdabc0 !important;
}

.sendMoneyLink {
    text-decoration: none !important;
}

.sendMoneyBtn {
    border-color: #FD3A6C !important;
    border-radius: 20px !important;
    width: 110px;
    height: auto;
    padding-top: 35px !important;
    padding-bottom: 22px !important;
}

.sendMoneyBtn:hover {
    background-color: #fdabc0 !important;
    text-decoration: none !important;
}

.info-currency {
    font-family: Poppins;
    font-style: normal;
    font-weight: bold;
    font-size: 64px;
    line-height: 96px;
    letter-spacing: 0.374px;
    color: #262626;
}

.peopleBtn {
    border-color: #F8F8F8 !important;
    border-radius: 20px !important;
    width: 110px;
    height: auto;
    padding-top: 25px !important;
    background-color: #F8F8F8 !important;
    padding-bottom: 16px !important;
    color: #262626 !important;
}

.peopleBtn:hover {
    background-color: #e7e7e7 !important;
}

.infoCard {
    display: flex;
    justify-content: center;
    background: #FFFFFF;
    border: 1px solid #B5B5B5;
    box-sizing: border-box;
    border-radius: 30px;
}

.cardBody {
    width: 90%;
}

.acType {
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0.374px;
    color: #B5B5B5;
}

.label-text {
    background-color: red;
    color: black;
}